@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    @include m.headings;
}

h1 {
    font-size: 4rem;
}
// h2 {
//     font-size: 3.6rem;
// }
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}

/* de esta forma buscamos por todas las clases que terminen en __heading */
/* ^ que comience | en SQL seria = Ejemplo% */
/* $ que finalize | en SQL seria = %Ejemplo */
/* * que contenga | en SQL seria = %Ejemplo% */
[class$="__heading"] {
    @include m.nombre-pagina;
}

[class$="__descripcion"] {
    @include m.descripcion-pagina;
}