@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin logo {
    color: v.$blanco;
    text-align: center;
    margin-bottom: 2rem;
    white-space: nowrap; /* evita el salto de linea */

    @include desktop {
        text-align: left;
        margin: 0;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin xl_desktop {
    @media (min-width: v.$xl_desktop) {
        @content;
    }
}

@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    font-size: 1.8rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 100%;
    display: inline-block;

    &:hover {
        background-color: darken($bgColor, 10%);
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin contenedor {
    // max-width: 120rem;
    // width: 95% es mejor la funcion de abajo;
    width: min(95%, 140rem);
    margin: 0 auto;
}

@mixin contenedor-md {
    width: min(95%, 80rem);
    margin: 0 auto;
}

@mixin contenedor-sm {
    width: min(95%, 60rem);
    margin: 0 auto;
}

@mixin headings {
    font-family: v.$fuente_principal;
    font-weight: v.$black;
    margin: 2rem 0;
}

@mixin nombre-pagina {
    @include headings;
    margin: 6rem 0 3rem 0;
    text-align: center;
}

@mixin descripcion-pagina {
    text-align: center;
    font-size: 1.8rem;
    font-weight: v.$delgada;
    color: v.$gris-oscuro;
    margin-bottom: 5rem;
}

/* En lugar de declarar varios tamaños de fuente lo hacemos mas dinamico de esta forma */
@mixin texto-fluido($min, $max) {
    font-size: clamp($min, -0.8rem + 10vw, $max);
    /* el valor del medio es el valor deseado, clamp es una funcion nueva de css, calp ya integra el calc */
}

@mixin texto-degradado($inicio, $final) {
    background: linear-gradient(to right, $inicio, $final);
    background-clip: text;
    /* Asigna el color al texto */
    -webkit-text-fill-color: transparent;
    /* como por defecto ya tiene un colo, le decimos que lo ignore y que tomo el de background */
}

@mixin menu-social($color, $size) {
    &__enlace {

        /* Esto es un pseudoelemento, como tal no existe, crea un elemento antes de los enlaces */
        &::before {
            color: $color;
            font-size: $size;
            display: inline-block;
            font-style: normal;
            text-rendering: auto;
            /* solo aplica a los vectores svg y hace que se vea correcta */
            -webkit-font-smothing: antialiased;
            /* solo aplica a los vectores svg y hace que se vea correcta */
            font-family: "Font Awesome 6 Brands";
            /* en este proyecto se esta utilizando la libreria font awesome */
        }

        &[href*="facebook.com"]::before {
            content: "\f39e";
        }

        &[href*="github.com"]::before {
            content: "\f09b";
        }

        &[href*="tiktok.com"]::before {
            content: "\e07b";
        }

        &[href*="instagram.com"]::before {
            content: "\f16d";
        }

        &[href*="youtube.com"]::before {
            content: "\f167";
        }

        &[href*="twitter.com"]::before {
            content: "\f099";
        }
    }

    &__ocultar {
        display: none;
    }
}

@mixin enlace {
    all: unset; /* este declaracion hace que borre todos los estilos por defecto */
    color: v.$blanco;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: v.$bold;

    &:hover {
        cursor: pointer;
        color: v.$azul;
    }
}

@mixin shadow {
    box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px; /* #13 https://getcssscan.com/css-box-shadow-examples */
}

@mixin reset-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin tags {
    @include reset-lista;
    display: flex;
    flex-wrap: wrap;
    gap: 1.5rem;
    margin-top: 1.5rem;

    /* el primer nivel de hijos */
    > li {
        background-color: v.$azul;
        color: v.$blanco;
        padding: 1rem;
        border-radius: 0.5rem;
        font-size: 1.4rem;
        font-weight: v.$bold;
    }
}

@mixin card {
    border: 1px solid lighten(v.$gris, 10);
    padding: 3rem;

    &__nombre {
        font-weight: v.$black;
        font-size: 2.6rem;
        margin: 2rem 0;
    }
}

@mixin bg-gradient($bg_color) {
    background-image: linear-gradient(to left top, $bg_color, lighten($bg_color, 15%));
}